.form_textarea__label {
	@include form_contact_label;
}

.form_textarea__element {
	@include form_contact_default;

	padding-top: 1rem;
	padding-bottom: 1rem;

	@include bp($point_2, min) {
		height: 24rem;
	}
	@include bp($point_3) {
		height: 18rem;
	}
}

.contact__form_btn {
	@include bp($point_3) {
		width: 100%;
		color: $white_2;
		background-color: $black_2;

		&:hover {
			background-color: $white_2;
			color: $black_2;
		}
	}
}
