.decor_img {
	background-color: $green;
}

.decor_img__bg_in {
	position: absolute;
	z-index: 2;
	top: 0;
	left: 50%;

	width: 100%;
	height: 100%;

	overflow: hidden;

	transform: translateX(-50%);

	&::after {
		content: '';
		position: absolute;
		top: 20.7vw;
		left: 0;

		width: 100%;
		height: 100%;

		.decor_img--coral_mod & {
			background-color: $coral;
		}
		.decor_img--green_mod & {
			display: none;
			// background-color: $green;
		}
	}
}

.decor_img__bg {
	object-position: top center;

	@include bp($point_0, min) {
		@include cover_img;
	}

	@include bp($point_0 - 1) {
		@include contain_img;
	}
}
