.hero {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

	padding-top: 12rem;

	@include bp($point_2, min) {
		min-height: 96rem;
		padding-bottom: 9.8rem;
	}

	@include bp($point_3) {
		min-height: 85.4rem;
		padding-bottom: 24.8rem;
	}
}

.hero__bg {
	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;
}

.hero__bg_img {
	@include cover_img;
}

.hero__content {
	position: relative;
	max-width: 100rem;
	margin: 0 auto;
	text-align: center;
}

.hero__subtitle {
	@include subtitle;

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 0.8rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 1.8rem;
		}
	}
}

.hero__title {
	@include h1;

	color: $black_2;

	@include bp($point_4 - 1) {
		max-width: 27rem;
		margin: 0 auto;
	}

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 2.8rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 1.8rem;
		}
	}

	strong {
		font-weight: 900;
	}
}

.hero__text {
	margin: 0 auto;

	@include bp($point_4, min) {
		max-width: 55rem;

		font-weight: 500;
		font-size: 2rem;
		line-height: 130%;
		letter-spacing: 0.03em;
		color: $black_2;
	}

	@include bp($point_4 - 1) {
		max-width: 29rem;
		font-weight: 400;
		font-size: 16px;
		line-height: 140%;
		letter-spacing: 0.02em;
		color: $black;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 3.8rem;
		}

		@include bp($point_3) {
			margin-bottom: 3rem;
		}
	}
}
