.main_content {
	padding-top: calc(var(--height-header) + 8.4rem);

	@include bp($point_2, min) {
		padding-bottom: 12.6rem;
	}
}

.main_content__in {
	max-width: 106rem;
}

.main_content__wrap {
	h1 {
		@include h2;
		font-family: $font_2;

		&:not(:last-child) {
			margin-bottom: 2.6rem;
		}
	}

	div {
		&:not(:last-child) {
			margin-bottom: 3.6rem;
		}
	}

	time {
		font-weight: 500;
		font-size: 2rem;
		line-height: 130%;
		letter-spacing: 0.03em;
	}

	cite {
		padding-left: 1rem;
		font-weight: 500;
		font-style: normal;
		font-size: 2rem;
		line-height: 130%;
		letter-spacing: 0.03em;

		&::before {
			content: '|';
			padding-right: 1rem;
		}
	}

	hr {
		border-color: $coral_2;
		height: 2px;
		&:not(:last-child) {
			margin-bottom: 4rem;
		}
	}

	figure {
		&:not(:last-child) {
			margin-bottom: 4.2rem;
		}
	}

	img {
		@include cover_img;

		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}

	figcaption {
		font-weight: 500;
		font-size: 1.5rem;
		line-height: 133%;
		letter-spacing: 0.02em;
	}

	h2 {
		@include h2;
		font-family: $font_2;

		&:not(:last-child) {
			margin-bottom: 2.4rem;
		}
	}

	h3 {
		@include h3;
		font-family: $font_2;

		&:not(:last-child) {
			margin-bottom: 2.6rem;
		}
	}

	h4 {
		@include h4;
		font-weight: 400;
		font-family: $font_2;

		&:not(:last-child) {
			margin-bottom: 3.4rem;
		}
	}

	h5 {
		@include subtitle;

		&:not(:last-child) {
			margin-bottom: 2.2rem;
		}
	}

	p {
		@include text;

		&:not(:last-child) {
			margin-bottom: 2.6rem;
		}
	}

	strong {
		font-weight: 700;
	}

	ol,
	li {
		list-style: unset;
	}

	ul {
		padding-left: 1rem;
		&:not(:last-child) {
			margin-bottom: 3.4rem;
		}

		li {
			display: flex;

			&:before {
				display: block;
				flex-shrink: 0;

				width: 0.3em;
				height: 0.3em;
				margin: 1rem 1.2rem 0 0;

				background-color: $black;
				border-radius: 50%;
				content: '';
			}
		}
	}

	ol {
		padding-left: 0.8rem;
		list-style-type: decimal;
		&:not(:last-child) {
			margin-bottom: 2.2rem;
		}

		li {
			margin-left: 2rem;
		}
	}

	li {
		@include text;

		&:not(:last-child) {
			margin-bottom: 1.2rem;
		}
	}

	a {
		position: relative;
		display: inline-flex;
		font-weight: 700;
		// text-decoration: underline;
		color: $martini;

		transition: color 0.3s ease;

		@include text;

		&::after {
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 1px;

			background-color: $black;
		}

		&:hover {
			text-decoration: none;
			color: $black_2;
		}

		&:not(:last-child) {
			margin-bottom: 4.6rem;
		}
	}
}

p.has-text-align-left {
	text-align: left;
}

p.has-text-align-center {
	text-align: center;
}

p.has-text-align-right {
	text-align: right;
}

p.has-x-large-font-size {
	font-weight: 400;
	font-size: 3rem;
	line-height: 136%;
	letter-spacing: 0.02em;

	&:not(:last-child) {
		margin-bottom: 1.8rem;
	}
}

p.has-large-font-size {
	font-weight: 500;
	font-size: 2rem;
	line-height: 130%;
	letter-spacing: 0.03em;

	&:not(:last-child) {
		margin-bottom: 2rem;
	}
}

.main_content__slider {
	overflow: hidden;
}
