.footer {
	width: 100%;
	min-height: var(--height-footer);
	flex: 0 0 auto;

	font-size: 1rem;
	background-color: $black_2;

	will-change: transform;

	@include bp($point_2, min) {
		padding: 11rem var(--gl-indent) 4.5rem;
	}

	@include bp($point_3) {
		padding: 2.4rem var(--gl-indent);
	}
}

.footer_in {
	max-width: 136rem;
	margin: 0 auto;
	width: 100%;
}

.footer__top {
	display: flex;
	align-items: center;

	width: 100%;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 7.6rem;
		}
	}

	@include bp($point_2, min) {
		gap: 1rem;
	}

	@include bp($point_3) {
		flex-direction: column;
	}
}

.footer__logo {
	display: flex;
	flex-shrink: 0;
	align-items: center;
	justify-content: center;

	width: 100%;
	max-width: 30.5rem;

	@include bp($point_2, min) {
		padding-top: 3rem;
	}

	@include bp($point_3) {
		flex-direction: column;

		margin-bottom: 2.1rem;
	}
}

.footer__picture {
	display: block;

	width: 100%;
	height: 100%;

	color: $white_2;

	@include bp($point_2, min) {
		&.footer__picture--small_mod {
			max-width: 9.5rem;
		}

		&:not(.footer__picture--small_mod) {
			max-width: 22.6rem;
		}
	}

	@include bp($point_3) {
		height: 12.6rem;
		&.footer__picture--small_mod {
			max-width: 7.5rem;
		}

		&:not(.footer__picture--small_mod) {
			max-width: 15.4rem;
		}
	}
}

.footer__img {
	display: block;

	width: 100%;
	height: 100%;

	object-fit: contain;
}

.footer__actions {
	flex: 1 1 auto;

	@include bp($point_3) {
		margin-bottom: 4.1rem;
	}
}

.footer__list {
	text-align: center;

	&.footer__list--top_mod {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;

		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}

	&.footer__list--bottom_mod {
		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 4.2rem;
			}

			@include bp($point_3) {
				margin-bottom: 2.2rem;
			}
		}

		@include bp($point_2, min) {
			display: block;

			.footer__logo & {
				display: none;
			}
		}

		@include bp($point_3) {
			display: none;

			.footer__logo & {
				display: block;
			}
		}
	}
}

.footer__item {
	font-family: $font_2;
	font-weight: 500;
	color: $white_2;

	&.footer__item--bottom_mod {
		font-size: 1.2rem;
		line-height: 1.35;
		letter-spacing: 0.02em;
		font-style: normal;

		@include bp($point_3) {
			font-size: 1.1rem;
		}

		&:not(:last-child) {
			@include bp($point_3) {
				margin-bottom: 1.6rem;
			}
		}
	}

	.footer__list--top_mod & {
		position: relative;

		&:not(:last-child) {
			margin-right: 3rem;

			&:after {
				content: '';
				position: absolute;
				top: 50%;
				right: -1.6rem;

				transform: translateY(-50%);

				width: 0.4rem;
				height: 0.4rem;

				border-radius: 50%;

				background-color: $white_2;
			}
		}
	}

	@include bp($point_2, min) {
		font-size: 2rem;
		line-height: 1.5;
		letter-spacing: 0.01em;
	}

	@include bp($point_3) {
		font-style: italic;
		font-size: 1.8rem;
		line-height: 1.4;

		&:not(.footer__list--top_mod &, .footer__list--bottom_mod &) {
			&:nth-child(2) {
				&:not(:last-child) {
					margin-bottom: 3rem;
				}
			}
		}
	}
}

.footer__link {
	color: $white_2;

	transition: opacity 0.3s ease;

	&:hover,
	&:focus {
		text-decoration: none;

		opacity: 0.8;
	}
}

.footer__bottom {
	position: relative;

	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

.footer__text {
	width: 100%;
	max-width: 54rem;

	text-align: center;
	font-size: 1.2rem;
	line-height: 1.35;
	letter-spacing: 0.02em;
	font-weight: 500;

	color: $white_2;

	@include bp($point_2, min) {
		padding-top: 4rem;

		border-top: 0.2px solid $white_2;
	}
}
