@mixin form_el_label {
	display: block;
	margin-bottom: 0.4rem;

	font-size: 1.6rem;
	color: $black;
}

@mixin form_el_default {
	display: block;
	width: 100%;
	height: $input_height;
	padding: $input_offset;
	font-size: $input_font_size;
	font-family: $font_1;
	color: $input_text_color;
	box-shadow: inset 0 0 0 1px $input_border_color;
	border: none;
	background-clip: padding-box;
	border-radius: $input_radius;
	caret-color: $black_2;

	transition: box-shadow 0.3s ease 0s;
	will-change: box-shadow;

	&:hover,
	&:focus {
		box-shadow: inset 0 0 0 1px $input_border_color_active;
	}

	&::-webkit-input-placeholder {
		color: $input_placeholder_color;
	}

	&::-moz-placeholder {
		color: $input_placeholder_color;
		opacity: 1;
	}

	&:-ms-input-placeholder {
		color: $input_placeholder_color;
	}

	// HIDE USER ICON
	&::-webkit-contacts-auto-fill-button {
		visibility: hidden;
		position: absolute;
		right: 0;
	}

	&[type='search'] {
		// HIDE CLEAR ICON
		&::-ms-clear,
		&::-ms-reveal {
			display: none;
		}

		// HIDE SAFARI DEFAULT BROWSER DECORATIONS
		&::-webkit-search-decoration,
		&::-webkit-search-cancel-button,
		&::-webkit-search-results-button,
		&::-webkit-search-results-decoration {
			display: none;
		}
	}

	&[type='date'] {
		// HIDE CALENDAR ICON
		&::-webkit-calendar-picker-indicator {
			opacity: 0;
		}
	}

	@at-root {
		// CLEAR DEFAULT SAFARI BG
		select#{&} {
			-webkit-tap-highlight-color: transparent;
		}
	}

	@include appearance(none);
}

@mixin form_el_newsletter {
	display: block;
	width: 100%;

	font-size: 1.8rem;
	font-family: $font_1;
	letter-spacing: 0.03em;
	color: $black_2;
	box-shadow: inset 0 0 0 1px $black_2;
	border: none;
	background-clip: padding-box;

	background-color: $white_2;

	transition: box-shadow 0.3s ease 0s;
	will-change: box-shadow;

	&:hover,
	&:focus {
		caret-color: $black_2;
		box-shadow: inset 0 0 0 1px $input_border_color_active;
	}

	&::-webkit-input-placeholder {
		color: $black_2;
	}

	&::-moz-placeholder {
		color: $black_2;
		opacity: 1;
	}

	&:-ms-input-placeholder {
		color: $black_2;
	}

	// HIDE USER ICON
	&::-webkit-contacts-auto-fill-button {
		visibility: hidden;
		position: absolute;
		right: 0;
	}

	&[type='search'] {
		// HIDE CLEAR ICON
		&::-ms-clear,
		&::-ms-reveal {
			display: none;
		}

		// HIDE SAFARI DEFAULT BROWSER DECORATIONS
		&::-webkit-search-decoration,
		&::-webkit-search-cancel-button,
		&::-webkit-search-results-button,
		&::-webkit-search-results-decoration {
			display: none;
		}
	}

	&[type='date'] {
		// HIDE CALENDAR ICON
		&::-webkit-calendar-picker-indicator {
			opacity: 0;
		}
	}

	@at-root {
		// CLEAR DEFAULT SAFARI BG
		select#{&} {
			-webkit-tap-highlight-color: transparent;
		}
	}

	@include appearance(none);

	@include bp($point_2, min) {
		height: 6.9rem;
		padding: 2rem 23.2rem 2rem 3.2rem;
		border-radius: 7.7rem;
	}

	@include bp($point_3) {
		border-radius: $gl_radius;
	}

	@include bp($point_3, $point_7) {
		padding: 1.8rem 16.2rem 1.8rem 3rem;
	}

	@include bp($point_7 - 1) {
		height: 6rem;
		padding: 1.8rem 11rem 1.8rem 3rem;
	}
}

@mixin form_contact_label {
	display: block;
	margin-bottom: 0.8rem;

	font-weight: 700;
	font-family: $font_2;
	line-height: 120%;
	color: $black;

	@include bp($point_2, min) {
		font-size: 2.8rem;
	}
	@include bp($point_3) {
		font-size: 2rem;
	}
}

