.form_input__label {
	@include form_el_label;
}

.form_input__element {
	@include form_el_default;
}

.contact__input_label,
.download_popup__input_label {
	@include form_contact_label;
}

.contact__input_element,
.download_popup__input_element {
	@include form_contact_default;
}

.form_newsletter__label {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0;
	white-space: nowrap;
	clip-path: inset(100%);
	clip: rect(0 0 0 0);
	overflow: hidden;
}

.form_newsletter__input {
	@include form_el_newsletter;
}
