    flex-wrap: nowrap;
    justify-content: center;
    align-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    padding: 11rem var(--gl-indent) 16rem;
    width: 100vw;
    height: var(--full-screen);
    background-color: #f6eedc;
    color: #141414;
    transform: translateX(100%);
    transition: transform ease 0.3s;
  }
