  }
  .header__list_item:nth-child(4) {
    margin-right: 26rem;
  }
  .header__list_item:after {
    position: absolute;
    bottom: -0.1rem;
    z-index: 2;
    display: block;
    width: calc((100% - 26rem) / 8);
    height: 0.4rem;
    opacity: 0;
