@import 'family';
@import 'reset';
@import 'fonts';
@import 'print';

//------------------------------------------------------------base styles
:root {
	--full-screen: calc(var(--vh, 1vh) * 100);

	--height-footer: 54.8rem;
	--gl-indent: 2rem;

	@include bp($point_1, min) {
		--height-header: 12rem;
	}
	@include bp($point_1 - 1) {
		--height-header: 10.8rem;
	}
}

html {
	height: 100%;
	font-family: $font_1;
	line-height: 1.33;

	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);

	@include bp(1921px, min) {
		font-size: calc(100vw / 1920 * 10);
	}

	@include bp(1920.5px, 1365.5px) {
		font-size: 10px;
	}

	@include bp(1365px, 1024px) {
		font-size: calc(100vw / 1920 * 15);
	}

	@include bp(1023.5px, 390px) {
		font-size: 10px;
	}

	@include bp(389.5px) {
		font-size: calc(100vw / 390 * 10);
	}
}

body {
	position: relative;

	display: flex;
	height: 100%;

	font-size: $font_size_base;
	color: $color_text_base;

	-webkit-text-size-adjust: none;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	background: $body_bg;

	@supports (display: grid) {
		display: block;
	}

	&.body--open_menu_state {
		overflow: hidden;
	}

	&.body--popup_open {
		overflow: hidden;
	}
}

.preloader {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	background-color: $white;

	opacity: 1;
	transition: opacity 0.3s;

	z-index: 1000;

	.body--loaded & {
		pointer-events: none;
		opacity: 0;
	}
}

.wrapper {
	position: relative;

	width: 100%;
	display: flex;
	flex-direction: column;
	min-height: 100%;
	height: auto !important;

	overflow-x: hidden;
}

.base {
	width: 100%;
	position: relative;
	// padding: var(--height-header) 0 0; прибрано для секцій hero

	flex: 1 0 auto;
}

a {
	text-decoration: none;
	color: $color_link_base;

	&:hover,
	&:focus {
		text-decoration: underline;
	}
}

//------------------------------------------------------------base styles###
