//------------------------------------------------------------layout
$color_white_style: $white_2;
$color_black_style: $black_2;

.header {
	position: fixed;
	top: 0;
	left: 0;

	height: var(--height-header);
	width: 100%;

	font-size: 1rem;

	will-change: transform;

	@include z-index(header);

	background-color: $black_2;
	transition: transform ease 1s, background-color ease 1s;

	&:not(.header--scroll_state) {
		@include bp($point_1 - 1) {
			background-color: rgba(#000000, 0);
		}
	}

	.body--open_menu_state &.header--pos_state {
		transform: translateY(0%);
	}

	&.header--pos_state {
		transform: translateY(-100%);
	}

	@include bp($point_1, min) {
		color: $white_2;
		border-bottom: 1px solid $white_2;
	}
}

//------------------------------------------------------------layout###

.header__in {
	position: relative;

	max-width: 158rem;
	width: 100%;
	height: 100%;
	margin: 0 auto;
}

.header__logo {
	position: absolute;
	top: 50%;
	left: 50%;
	z-index: 10;

	height: 7.1rem;
	width: 13.6rem;

	transform: translate(-50%, -50%);
	color: inherit;
}

.header__logo_img {
	@include contain_img;
	position: absolute;
	transition: opacity ease 0.3s;

	&.header__logo_img--black_mod {
		opacity: 0;
	}

	.body--open_menu_state &.header__logo_img--black_mod {
		opacity: 1;
	}

	.body--open_submenu_state &.header__logo_img--black_mod {
		opacity: 0;
	}

	.body--open_menu_state &.header__logo_img--white_mod {
		opacity: 0;
	}

	.body--open_submenu_state &.header__logo_img--white_mod {
		opacity: 1;
	}

	header:not(.header--scroll_state) & {
		@include bp($point_1 - 1) {
			&.header__logo_img--black_mod {
				opacity: 1;
			}

			&.header__logo_img--white_mod {
				opacity: 0;
			}

			.body--header_white_mod:not(.body--open_menu_state) &.header__logo_img--black_mod {
				opacity: 0;
			}

			.body--header_white_mod:not(.body--open_submenu_state) &.header__logo_img--white_mod {
				opacity: 1;
			}

			.body--open_submenu_state &.header__logo_img--black_mod {
				opacity: 0;
			}

			.body--open_submenu_state &.header__logo_img--white_mod {
				opacity: 1;
			}
		}
	}
}

.header__nav {
	@include bp($point_1, min) {
		padding-top: 6.4rem;
	}
	@include bp($point_1 - 1) {
		@include flex_block(column, nowrap, center, center, center);
		position: absolute;
		top: 0;
		left: 0;

		padding: 11rem var(--gl-indent) 16rem;
		width: 100vw;
		height: var(--full-screen);
		background-color: $color_white_style;
		color: $black_2;
		transform: translateX(100%);
		transition: transform ease 0.3s;

		.body--open_menu_state & {
			transform: translateX(0);
		}
	}
}

.header__nav_in {
	@include bp($point_1 - 1) {
		width: 100%;
		padding: 0 1rem; // for scroll
		overflow-y: auto;
		overflow-x: hidden;
	}
}

.header__list {
	@include bp($point_1, min) {
		@include flex_block(row, wrap, flex-start, flex-start, center);
		height: 100%;
	}
}

.header__list_item {
	@include flex_block(row, wrap, center, center, center);

	$item-offset: 26rem;

	&:hover {
		text-decoration: none;

		&:after {
			opacity: 1;
		}
	}

	@include bp($point_1, min) {
		padding: 0 1rem;
		width: calc((100% - $item-offset) / 8);

		&:nth-child(4) {
			margin-right: $item-offset;
		}

		&:after {
			position: absolute;
			bottom: -0.1rem;
			// left: 0;
			z-index: 2;

			display: block;
			width: calc((100% - $item-offset) / 8);
			height: 0.4rem;

			opacity: 0;
			transition: opacity ease 0.3s;
			background-color: $white_2;
			content: '';

			pointer-events: none;
		}
	}
	@include bp($point_1 - 1) {
		position: relative;

		max-width: 45rem;
		width: 100%;
		margin-left: auto;
		margin-right: auto;

		&:not(:last-child) {
			margin-bottom: 1.6rem;
		}
	}
}

.header__list_item_btn {
	@include flex_block(row, nowrap, center, center, center);
	position: relative;
	z-index: 1;

	color: inherit;
	cursor: pointer;

	&:focus,
	&:hover {
		text-decoration: none;
	}

	@include bp($point_1 - 1) {
		text-align: center;
		width: 100%;
		padding: 0 3rem;

		transition: opacity ease 0.3s;

		&:hover {
			opacity: 0.6;
		}
	}
}

.header__list_item_btn_title {
	color: inherit;

	@include bp($point_1, min) {
		font-family: $font_1;
		font-size: 1.5rem;
		line-height: 100%;
		font-weight: 700;
		letter-spacing: 0.02em;
	}
	@include bp($point_1 - 1) {
		font-family: $font_2;
		line-height: 135%;
		font-size: 3.4rem;
		font-weight: 500;
	}
}

.header__list_item_btn_icon {
	display: block;

	width: 1rem;
	height: 1.6rem;
	margin-left: 1rem;

	.header__list_item:hover & {
		transform: rotate(180deg);
	}

	transition: transform ease 0.3s;

	@include bp($point_1 - 1) {
		display: none;
	}
}

.header__list_item_btn_mobile_icon {
	@include bp($point_1, min) {
		display: none;
	}
	@include bp($point_1 - 1) {
		position: absolute;
		// right: var(--gl-indent);
		right: 0.5rem;
		display: block;
		width: 2rem;
		height: 2.2rem;
	}
}

.header__sublist_w {
	@include bp($point_1, min) {
		opacity: 0;
		pointer-events: none;

		position: absolute;
		top: 0;
		left: 0;

		padding-top: calc(var(--height-header) / 2);
		margin-top: calc(var(--height-header) / 2);
		width: $page_width;
		transition: opacity ease 0.3s;

		.header__list_item:hover & {
			opacity: 1;
			pointer-events: auto;
		}
	}

	@include bp($point_1 - 1) {
		@include flex_block(column, nowrap, center, center, center);
		position: fixed;
		top: 0;
		right: 0;
		z-index: 2;

		width: calc(100vw - 2rem);
		// width: 100vw;
		height: var(--full-screen);
		padding: 11rem calc(var(--gl-indent) + 2rem) 16rem var(--gl-indent);

		text-align: center;
		background-color: $black_2;
		transition: transform ease 0.3s;

		transform: translateX(100%);

		.header__list_item--open_submenu_state & {
			transform: translateX(0);
		}
	}
}

.header__sublist_back {
	@include bp($point_1, min) {
		display: none;
	}

	@include bp($point_1 - 1) {
		position: relative;

		max-width: 45rem;
		width: 100%;
		margin-bottom: 4.6rem;
	}
}

.header__sublist_back_btn {
	padding: 0 4rem;
	width: 100%;

	color: $white_2;
	font-size: 3.4rem;
	font-weight: 500;
	font-family: $font_2;
	line-height: 125%;
	text-align: center;
	cursor: pointer;
	transition: opacity ease 0.3s;

	&:hover {
		opacity: 0.6;
	}
}

.header__sublist_back_arrow {
	position: absolute;
	top: 1.1rem;
	left: 1.2rem;

	height: 2rem;
	width: 1rem;

	transform: rotate(-180deg);
}

.header__sublist {
	position: relative;

	@include bp($point_1, min) {
		@include flex_block(column, wrap, flex-start, flex-start);
		width: 100%;
		max-width: $page_width;
		height: 100%;
		max-height: 28rem;
		padding: 4rem 3rem 3rem 1rem;
		margin: 0 -5.2rem -0.8rem 0;

		border-top: 1px solid $white_2;
		border-bottom: 1px solid $white_2;

		&:after {
			position: absolute;
			top: 0;
			left: -100vw;
			z-index: -1;
			display: block;
			width: 200vw;
			height: 100%;

			background-color: $black_2;
			opacity: 0;

			transition: opacity ease 0.3s;
			content: '';
		}

		.header__list_item:hover & {
			&:after {
				opacity: 1;
			}
		}
	}

	@include bp($point_1 - 1) {
		@include flex_block(column, nowrap, flex-start, flex-start);
		padding: 0 1rem;
		overflow-y: auto;
		overflow-x: hidden;
	}
}

.header__sublist_item {
	max-width: 39.2rem;
	width: 100%;

	@include bp($point_1, min) {
		padding: 0 5.2rem 0.8rem 0;
	}

	@include bp($point_1 - 1) {
		&:not(:last-child) {
			margin-bottom: 0.8rem;
		}
	}
}

.header__nav_link {
	position: relative;

	display: block;

	transition: transform ease 0.3s, opacity ease 0.3s;

	&:hover {
		text-decoration: none;

		@include bp($point_1, min) {
			transform: translateX(3rem);

			&:after {
				opacity: 1;
			}
		}
		@include bp($point_1 - 1) {
			opacity: 0.6;
		}
	}

	@include bp($point_1, min) {
		font-family: $font_2;
		font-size: 2.4rem;
		line-height: 120%;
		color: inherit;

		&:after {
			position: absolute;
			top: 0;
			left: -3rem;
			display: block;
			opacity: 0;
			transition: opacity ease 0.3s 0.1s;
			content: '→';
		}
	}

	@include bp($point_1 - 1) {
		font-size: 2rem;
		line-height: 150%;
		letter-spacing: 0.032em;
		color: $white_2;
		font-weight: 500;
	}
}

// ************************************
// *************** MENU ***************
// ************************************

.header__menu_trigger {
	$burgerHeight: 2rem;

	position: fixed;
	top: 4.3rem;
	right: 3rem;
	z-index: 5;

	width: 3rem;
	height: $burgerHeight;

	cursor: pointer;

	&::before,
	&::after,
	span {
		position: absolute;
		left: 0;
		top: 50%;

		width: 100%;
		height: 2px;

		transition: transform 0.3s ease;
		will-change: transform;

		background-color: $color_white_style;

		.body--open_menu_state & {
			background-color: $black_2;
		}

		.body--open_menu_state.body--open_submenu_state & {
			background-color: $white_2;
		}

		@include bp($point_1 - 1) {
			header:not(.header--scroll_state) & {
				background-color: $black_2;
			}

			.body--header_white_mod:not(.body--open_menu_state, .body--open_submenu_state)
				header:not(.header--scroll_state)
				& {
				background-color: $white_2;
			}
		}
	}

	&::before {
		content: '';
		transform: translateY(calc($burgerHeight / 2 * -1));
	}

	&::after {
		content: '';
		transform: translateY(calc($burgerHeight / 2));
	}

	.body--open_menu_state & {
		span {
			transform: scale(0);
		}

		&::before {
			transform: rotate(-45deg);
		}

		&::after {
			transform: rotate(45deg);
		}
	}

	@include bp($point_1, min) {
		display: none;
	}
	@include bp($point_1 - 1) {
		display: block;
	}
}

.header__nav_info {
	@include bp($point_1, min) {
		display: none;
	}

	@include bp($point_1 - 1) {
		@include flex_block(column, nowrap, center, center, center);

		position: absolute;
		left: 50%;
		bottom: 4rem;
		z-index: 10;

		max-width: 22.8rem;
		width: 100%;

		transform: translateX(-50%);
	}
}

.header__nav_info_logo {
	width: 5.3rem;
	height: 3.1rem;

	margin-bottom: 2.1rem;
}

.header__nav_info_img {
	@include contain_img;
}

.header__nav_info_rights {
	width: 100%;
	padding-top: 1.5rem;
	border-top: 1px solid $black_2;

	text-align: center;
	font-size: 1.1rem;
	font-weight: 500;
	color: $black_2;

	.body--open_submenu_state & {
		color: $white_2;
		border-top: 1px solid $white_2;
	}
}

.header__nav_info_img {
	&.header__nav_info_img--black_mod {
		display: block;
	}

	&.header__nav_info_img--white_mod {
		display: none;
	}

	.body--open_submenu_state &.header__nav_info_img--black_mod {
		display: none;
	}

	.body--open_submenu_state &.header__nav_info_img--white_mod {
		display: block;
	}
}
