.hero_event {
	&.hero_event--color_white_mod {
		background-color: none;
	}

	&.hero_event--color_black_mod {
		background-color: $black_2;
	}

	@include bp($point_2, min) {
		padding: var(--height-header) 0 0;

		&:not(.hero_event--color_white_mod, .hero_event--color_black_mod) {
			&::after,
			&::before {
				content: '';
				position: absolute;
				top: 0;

				width: 50%;
				height: 100%;
				background-color: $coral_2;
			}
		}

		&.hero_event--color_coral_mod {
			&::after {
				background-color: $coral_2;
			}
			&::before {
				background-color: $coral;
			}
		}

		&.hero_event--color_green_mod {
			&::after {
				background-color: $martini;
			}
			&::before {
				background-color: $green;
			}
		}

		&::after {
			left: 0;
		}

		&::before {
			right: 0;
		}
	}

	@include bp($point_3) {
		padding-top: 15.2rem;
		padding-bottom: 0;

		text-align: center;

		&.hero_event--color_black_mod {
			padding-top: 15.6rem;
			padding-bottom: 8.6rem;
		}

		&:not(.hero_event--revers_mod) {
			&.hero_event--color_coral_mod {
				background-color: $coral_2;
			}

			&.hero_event--color_green_mod {
				background-color: $martini;
			}
		}

		&.hero_event--revers_mod.hero_event--color_coral_mod {
			background-color: $coral;
		}

		&.hero_event--color_green_mod.hero_event--revers_mod {
			background-color: $green;
		}
	}
}

.hero_event__row {
	position: relative;
	z-index: 2;

	@include bp($point_2, min) {
		display: flex;
		align-items: stretch;
	}
}

.hero_event__col {
	position: relative;

	@include bp($point_2, min) {
		width: 50%;
		flex-shrink: 0;
	}

	&:not(:last-child, .hero_event--color_black_mod &) {
		@include bp($point_3) {
			margin-bottom: 4.4rem;
		}
	}

	&:nth-child(odd) {
		@include bp(1440px, min) {
			// padding: 5.8rem 7.2rem 21.4rem 23.5rem;
			padding: 5.8rem 7.2rem 21.4rem min(23.5rem, 13vw);

			.hero_event--revers_mod & {
				padding: var(--gl-indent) var(--gl-indent) 8rem 14.5rem;
			}
		}

		@include bp(1439px, $point_2) {
			padding: 5.8rem 6rem 21.4rem 5rem;

			.hero_event--revers_mod & {
				padding: var(--gl-indent) var(--gl-indent) 9.2rem 5rem;
			}
		}

		@include bp($point_2, min) {
			display: flex;
			flex-direction: column;
			justify-content: center;
			// align-items: flex-end;

			.hero_event--revers_mod & {
				order: 2;
			}

			.hero_event--color_white_mod & {
				padding-bottom: 15.6rem;
			}
		}
	}

	&:nth-child(even) {
		@include bp($point_2, min) {
			.hero_event--revers_mod & {
				order: 1;
			}

			&:not(.hero_event--color_white_mod &, .hero_event--color_black_mod &) {
				padding-top: 6.2rem;
				align-self: end;
			}
		}

		@include bp($point_3) {
			margin: 0 calc(var(--gl-indent) * -1);

			.hero_event--color_black_mod & {
				display: none;
			}
		}
	}
}

.hero_event__wrap {
	&:not(.hero_event--width_content_mod &, .hero_event--revers_mod &) {
		max-width: 52.6rem;
	}

	// .hero_event--revers_mod & {
	// 	// margin: 0 auto;
	// 	max-width: 52rem;
	// }
	.hero_event--revers_mod &,
	.hero_event--color_black_mod & {
		max-width: 56.4rem;
	}

	@include bp($point_3) {
		margin: 0 auto;
	}
}

.hero_event__content {
	display: flex;
	flex-direction: column;

	@include bp($point_2, min) {
		justify-content: flex-start;
	}

	@include bp($point_3) {
		align-items: center;
	}

	&:not(.hero_event--revers_mod &) {
		.hero_event--color_green_mod & {
			color: $white_2;
		}

		.hero_event--color_black_mod & {
			color: $white_2;
		}
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 4rem;

			.hero_event--color_black_mod & {
				margin-bottom: 3rem;
			}
		}

		@include bp($point_3) {
			margin-bottom: 3rem;
		}
	}

	h6 {
		@include subtitle;

		&:not(:last-child) {
			margin-bottom: 1.4rem;
		}
	}

	h1 {
		@include h2;
		font-family: $font_2;

		&:not(:last-child) {
			margin-bottom: 1.4rem;
		}
	}

	address {
		font-style: normal;
		text-decoration: underline;

		&:not(:last-child) {
			margin-bottom: 3.4rem;
		}

		@include bp($point_2, min) {
			align-self: start;
			font-weight: 500;
			font-size: 2rem;
			line-height: 130%;
			letter-spacing: 0.03em;
		}

		@include bp($point_3) {
			font-weight: 700;
			font-size: 16px;
			line-height: 140%;
			letter-spacing: 0.02em;
		}
	}

	em {
		display: block;
		font-style: italic;
		font-weight: 400;
		font-size: 3.2rem;
		line-height: 120%;
		font-family: $font_2;

		&:not(:last-child) {
			margin-bottom: 1.6rem;
		}
	}

	p {
		@include text;

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 4rem;

				.hero_event--color_black_mod & {
					margin-bottom: 2.6rem;
				}
			}

			@include bp($point_3) {
				margin-bottom: 3rem;

				.hero_event--color_black_mod & {
					margin-bottom: 1.8rem;
				}
			}
		}

		@include bp($point_3) {
			max-width: 29rem;
			margin: 0 auto;

			.hero_event--color_black_mod & {
				max-width: 32rem;
			}
		}
	}

	a {
		@include text;

		font-weight: 700;
		text-decoration: underline;
		color: $white_2;

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 1.2rem;
			}

			@include bp($point_3) {
				margin-bottom: 1rem;
			}
		}

		&:hover,
		&:focus {
			text-decoration: none;
			&::after {
				opacity: 0;
			}
		}

		@include bp($point_2, min) {
			align-self: start;
		}

		@include bp($point_3) {
			align-self: center;
		}
	}
}

.hero_event__btn {
	.hero_event--color_black_mod & {
		color: $white_2;
		border-color: $white_2;

		&:hover,
		&:focus {
			color: $black_2;
			border-color: $black_2;
			background-color: $coral;
		}
	}
}

.hero_event__icon {
	cursor: pointer;
	transition: color 0.3s ease;

	&:hover,
	&:focus {
		color: $color_link_base !important;
	}

	@include bp($point_2, min) {
		position: absolute;

		width: 4rem;
		height: 1.6rem;
		color: $black_2;

		transition: color 0.3s ease;

		.hero_event--color_green_mod & {
			color: $black_2;
		}

		&:not(.hero_event--revers_mod &) {
			.hero_event--color_green_mod & {
				color: $white_2;
			}
		}

		.hero_event--color_black_mod & {
			color: $white_2;
		}
	}

	@include bp(1440px, min) {
		left: 23.5rem;
		top: 85%;

		.hero_event--revers_mod & {
			left: 14.5rem;
		}
	}

	@include bp(1439px, $point_2) {
		left: 5rem;
		top: 90%;
	}

	@include bp($point_3) {
		display: none;
	}
}

.hero_event__pic {
	position: relative;
	display: block;
	width: 100%;

	&::after {
		content: '';
		display: block;

		@include bp($point_2, min) {
			padding-bottom: 90.8%;

			.hero_event--color_white_mod &,
			.hero_event--color_black_mod & {
				padding-bottom: 97.8%;
			}
		}

		@include bp($point_3) {
			padding-bottom: 102.4%;
		}
	}

	mask-position: 50% 0;
	-webkit-mask-position: 50% 0;
	mask-repeat: no-repeat;
	-webkit-mask-repeat: no-repeat;

	.hero_event--mask_1_mod & {
		mask-image: url('../images/masks/mask_1.svg');
		-webkit-mask-image: url('../images/masks/mask_1.svg');
	}

	.hero_event--mask_2_mod & {
		mask-image: url('../images/masks/mask_2.svg');
		-webkit-mask-image: url('../images/masks/mask_2.svg');
	}

	.hero_event--mask_3_mod & {
		mask-image: url('../images/masks/mask_3.svg');
		-webkit-mask-image: url('../images/masks/mask_3.svg');
	}

	.hero_event--mask_4_mod & {
		mask-image: url('../images/masks/mask_4.svg');
		-webkit-mask-image: url('../images/masks/mask_4.svg');
	}

	@include bp($point_2, min) {
		// padding-bottom: 103%;

		.hero_event--mask_1_mod & {
			mask-size: 85%, 100%;
			-webkit-mask-size: 85%, 100%;
		}

		.hero_event--mask_2_mod & {
			mask-size: 80%, 100%;
			-webkit-mask-size: 80%, 100%;
		}

		.hero_event--mask_3_mod & {
			mask-size: 82%, 100%;
			-webkit-mask-size: 82%, 100%;
		}

		.hero_event--mask_4_mod & {
			mask-size: 85%, 100%;
			-webkit-mask-size: 85%, 100%;
		}
	}

	@include bp($point_3) {
		.hero_event--mask_1_mod &,
		.hero_event--mask_2_mod &,
		.hero_event--mask_4_mod & {
			mask-size: 100%, 100%;
			-webkit-mask-size: 100%, 100%;
		}

		.hero_event--mask_3_mod & {
			mask-size: 94%, 100%;
			-webkit-mask-size: 94%, 100%;
		}
	}
}

.hero_event__img {
	position: absolute;
	top: 0;
	left: 0;

	@include cover_img;
}

.hero_event__slider {
	position: relative;
}

.hero_event .swiper-pagination {
	@include bp($point_1, min) {
		margin-bottom: 11rem;
	}

	@include bp($point_1, $point_2) {
		margin-bottom: 5rem;
	}

	@include bp($point_3) {
		margin-bottom: 3.8rem;
	}
}

.hero_event .swiper-pagination-bullet {
	border: 1px solid $white_2;
	background-color: transparent;

	transition: background 0.3s ease, opacity 0.3s ease;

	@include bp($point_2, min) {
		width: 1.5rem;
		height: 1.5rem;
		margin: 0 1.6rem !important;
	}

	@include bp($point_3) {
		width: 1.2rem;
		height: 1.2rem;
		margin: 0 1rem !important;
	}
}

.hero_event .swiper-pagination-bullet-active {
	background-color: $white_2;
}

.hero_event .swiper-pagination-bullet {
	opacity: 1 !important;
}
